@import "../../../styles/constants.scss";

.expandable {
  // border: 2px solid black;
  height: $headerH;
  // box-sizing: border-box;

  &:hover {
    background: $headerGradient;
    border-bottom: 2px solid $headerGradientEndC;
    height: $headerH;
  }
}

.clickable:hover {
  background: $headerGradient;
  border-bottom: 2px solid $headerGradientEndC;
  height: $headerH;
}

.mobile {
  .expandable,
  .clickable,
  .selected,
  .expandable.selected,
  .expandable:hover,
  .clickable:hover,
  .selected:hover {
    height: $headerMobileH;
  }
}

/*--Header--*/
