  }

  /* Tooltip container */
  .tooltip {
    position: relative;
    display: inline-block;
    border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
  }

  /* Tooltip text */
  .tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;

    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    top: 125%;
    left: 50%;
    margin-left: -60px;

    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
  }

  /* Tooltip arrow */
  .tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }

  /* Show the tooltip text when you mouse over the tooltip container */
  .tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
  }

  .hamburger-button {
    color: white;
    background: red;
    border: 1px solid white;
    border-radius: 4px;
    margin-top: -1px;
    vertical-align: middle;
    // line-height: 20px;
  }

  .hamburger {
    // border: 1px solid white;
  }

  .glitz {
    background: linear-gradient(270deg, #00ffbd, #e98fea, #8fcdea);
    background-size: 600% 600%;

    -webkit-animation: ClickMeGradient 0.6s ease infinite;
    -moz-animation: ClickMeGradient 0.6s ease infinite;
    animation: ClickMeGradient 0.6s ease infinite;
  }

  .notification {
    position: relative;

    .badge {
      position: absolute;
      font-size: 10px;
      top: -30px;
      left: 18px;
      height: 20px;
      width: 20px;
      text-align: center;
      line-height: 20px;
      // padding: 2px 2px;
      border-radius: 50%;
      background: red;
      color: white;
    }
  }

  @-webkit-keyframes ClickMeGradient {
    0% {
      background-position: 0% 48%;
    }
    50% {
      background-position: 100% 53%;
    }
    100% {
      background-position: 0% 48%;
    }
  }
  @-moz-keyframes ClickMeGradient {
    0% {
      background-position: 0% 48%;
    }
    50% {
      background-position: 100% 53%;
    }
    100% {
      background-position: 0% 48%;
    }
  }
  @keyframes ClickMeGradient {
    0% {
      background-position: 0% 48%;
    }
    50% {
      background-position: 100% 53%;
    }
    100% {
      background-position: 0% 48%;
    }
  }

  @media screen and (max-width: 830px) {
    // font-size: $fontSize-tablet-1;
    .submenu ul {
      font-size: $fontSize-tablet-2;
      overflow-y: auto;
      overflow-x: hidden;
    }
  }

  @media screen and (max-width: 400px) {
    font-size: $fontSize-mobile-2;
    .submenu ul {
      font-size: $fontSize-mobile-3;
    }
  }

  @media screen and (max-width: 340px) {
    font-size: $fontSize-mobile-3;
    .submenu ul {
      font-size: $fontSize-mobile-4;
    }
  }

  @media screen and (max-width: 314px) {
    font-size: $fontSize-mobile-5;
    .submenu ul {
      font-size: $fontSize-mobile-5;
    }
  }
}

